import React, { useEffect } from 'react'

const LazyLoader = ({ buffer }) => {

  const windowGlobal = typeof window !== 'undefined' && window;

  const offset = el => {
    const rect = el.getBoundingClientRect();
    const scrollLeft = window.pageXOffset || document.documentElement.scrollLeft;
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
  }

  useEffect(() => {
    if (!windowGlobal) {
      return;
    }
    let lazyloadThrottleTimeout;
    const lazyLoad = () => {
      if (lazyloadThrottleTimeout) {
        clearTimeout(lazyloadThrottleTimeout);
      }
      const lazyLoadedImages = windowGlobal.document.querySelectorAll('img.lazy');
      lazyloadThrottleTimeout = setTimeout(() => {
        const scrollTop = windowGlobal.pageYOffset;
        lazyLoadedImages.forEach(img => {
          if (offset(img).top < (windowGlobal.innerHeight + scrollTop + buffer)) {
            img.src = img.dataset.src;
            img.classList.remove('lazy');
          }
        });
        if(lazyLoadedImages.length === 0) {
          windowGlobal.document.removeEventListener('scroll', lazyLoad);
          windowGlobal.removeEventListener('resize', lazyLoad);
          windowGlobal.removeEventListener('orientationChange', lazyLoad);
          windowGlobal.removeEventListener('load', lazyLoad);
        }
      }, 20);
    };
    windowGlobal.addEventListener('load', lazyLoad);
    windowGlobal.document.addEventListener('scroll', lazyLoad);
    windowGlobal.addEventListener('resize', lazyLoad);
    windowGlobal.addEventListener('orientationChange', lazyLoad);
  }, []);

  return (
    <>
    </>
  );
};

export default LazyLoader;